import React, { useEffect, useState } from "react";

const Search = ({ onChange }) => {
  const [input, setInput] = useState("");
  function handleChange(event) {
    setInput(event.target.value);
    onChange(event.target.value);
  }


  return (
    <div className="shadow-lg fixed bg-white w-full h-16 z-50">
      <div className="container mx-auto flex h-16 items-center space-x-3 px-6">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 24 24"
          strokeWidth="1.5"
          stroke="currentColor"
          className="w-8 h-8 stroke-blue-700 fill-none"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M21 21l-5.197-5.197m0 0A7.5 7.5 0 105.196 5.196a7.5 7.5 0 0010.607 10.607z"
          />
        </svg>
        <input
          className="text-lg outline-none py-4 w-full"
          placeholder="Cerca in tutte le pubblicazioni"
          value={input}
          onChange={handleChange}
        ></input>
      </div>
    </div>
  );
};
export default Search;
