import Pubblicazione from "./pubblicazione";
import {
  WindowScroller,
  CellMeasurer,
  CellMeasurerCache,
  List,
  AutoSizer,
} from "react-virtualized";

const Pubblicazioni = ({ pubblicazioni, baseUrlAllegati }) => {
  const cache = new CellMeasurerCache({ defaultHeight: 100, fixedWidth: true });
  const rowRender = ({ key, index, isScrolling, isVisible, style, parent }) => {
    return (
      <CellMeasurer
        cache={cache}
        columnIndex={0}
        key={key}
        parent={parent}
        rowIndex={index}
      >
        {({ registerChild }) => (
          <div ref={registerChild} style={style} className="px-6">
            <Pubblicazione
              pubblicazione={pubblicazioni[index]}
              baseUrlAllegati={baseUrlAllegati}
            />
          </div>
        )}
      </CellMeasurer>
    );
  };

  return (
    <WindowScroller>
      {({ height, isScrolling, onChildScroll, scrollTop }) => (
        <AutoSizer disableHeight>
          {({ width }) => (
            <List
              autoHeight
              width={width}
              height={height}
              isScrolling={isScrolling}
              onScroll={onChildScroll}
              scrollTop={scrollTop}
              rowCount={pubblicazioni.length}
              deferredMeasurementCache={cache}
              rowHeight={cache.rowHeight}
              rowRenderer={rowRender}
              overscanRowCount={10}
            />
          )}
        </AutoSizer>
      )}
    </WindowScroller>
  );
};

export default Pubblicazioni;
