import Pubblicazioni from "./components/pubblicazioni";
import BarraDiStato from "./components/barradistato";
import { Helmet } from "react-helmet";
import Header from "./components/header";
import Search from "./components/search";
import { useEffect, useState } from "react";
function App() {
  const queryParms = new URLSearchParams(window.location.search);
  const idPub = queryParms.get("idPub") ? queryParms.get("idPub") : "";
  const [data, setData] = useState([]);
  const [pubblicazioniTrovate, setPubblicazioniTrovate] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const baseUrlAllegati =
    "https://albopretorio.sipalinformatica.it/sipal_ap/servlet/";
  const getData = () => {
    fetch(
      "https://raw.githubusercontent.com/car79/AlboSolarinoJson/main/pubblicazioni.json",
      { cache: "no-store" }
    )
      .then((resp) => {
        if (resp.ok) {
          return resp.json();
        }
        throw resp;
      })
      .then((json) => {
        setIsLoading(true);
        json.sort(
          (a, b) =>
            parseInt(b.anno + "" + String(b.num).padStart(8, "0")) -
            parseInt(a.anno + "" + String(a.num).padStart(8, "0"))
        );
        setData(json);
        setPubblicazioniTrovate(json);
        if (Number(idPub)) {
          setPubblicazioniTrovate(
            json.filter((row) => {
              return (
                parseInt(row.anno + "" + String(row.num).padStart(8, "0")) ===
                Number(idPub)
              );
            })
          );
        }
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };
  useEffect(() => {
    getData();
  }, []);

  if (isLoading)
    return (
      <>
        <Search /> <Header />
        <div className="container mx-auto px-6 pt-6">
          <h1 className="text-lg font-semibold">Caricamento in corso...</h1>
        </div>
      </>
    );
  return (
    <>
      {idPub && pubblicazioniTrovate.length === 1 && (
        <Helmet>
          <title>{pubblicazioniTrovate[0].oggetto}</title>
          <meta property="title" content={pubblicazioniTrovate[0].oggetto} />
          <meta property="quote" content={pubblicazioniTrovate[0].oggetto} />
          <meta name="description" content={pubblicazioniTrovate[0].oggetto} />
          {/* <meta property="image" content={image} /> */}
          <meta property="og:locale" content="it_IT" />
          <meta property="og:type" content="website" />
          <meta property="og:title" content={pubblicazioniTrovate[0].oggetto} />
          <meta property="og:quote" content={pubblicazioniTrovate[0].oggetto} />
          <meta property="og:hashtag" content={"#prova"} />
          {/* <meta property="og:image" content={image} />
          <meta content="image/*" property="og:image:type" /> */}
          <meta
            property="og:url"
            content={
              "https://car79.github.io/AlboPretorioSolarinoApp/?idPub=" +
              pubblicazioniTrovate[0].anno +
              "" +
              String(pubblicazioniTrovate[0].num).padStart(8, "0")
            }
          />
          <meta property="og:site_name" content="AlboSolarino" />
          <meta
            property="og:description"
            content={pubblicazioniTrovate[0].oggetto}
          />
        </Helmet>
      )}

      <Search
        onChange={(val) => {
          if (val) {
            const dt = data.filter((row) => {
              return Object.values(row)
                .join("")
                .toLowerCase()
                .includes(val.toLowerCase());
            });
            setPubblicazioniTrovate(dt);
          } else {
            setPubblicazioniTrovate(data);
          }
        }}
      />
      <Header />
      <BarraDiStato
        pubblicazioniTot={data.length}
        pubblicazioniTotTrovati={pubblicazioniTrovate.length}
      />
      <div className="container mx-auto ">
        <div className="my-6">
          {pubblicazioniTrovate.length ? (
            <Pubblicazioni
              pubblicazioni={pubblicazioniTrovate}
              baseUrlAllegati={baseUrlAllegati}
            />
          ) : (
            <div className="px-6">
              <h1 className=" pb-6 font-semibold">
                Nessuna pubblicazione trovata{" "}
              </h1>
              <span>
                Controlla se la pubblicazione si trova nello{" "}
                <a
                  target={"_blank"}
                  className="underline decoration-transparent hover:decoration-inherit transition duration-300 ease-in-out text-blue-800 font-bold leading-tight uppercase whitespace-nowrap"
                  href="https://solarino.maggiolicloud.it/atti/Delibere.aspx"
                >
                  storico atti
                </a>{" "}
                del sito istituzionale dell'ente.
              </span>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

export default App;
